* {
    margin: 0;
    padding: 0;
}

.navigationBar {
    z-index: 10000;
    position: fixed;
    top: 0;
    width: 100%;
    height: 80px;
    background-color: rgba(245, 245, 245, 0.9);
    box-shadow: 1px 1px 20px #4747475e;
    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
}

.navigationBarTitle {
    position: relative;
    top: calc((100% - 33px) / 2);
    left: 20px;
    font-family: "Helvetica";
    font-size: 27px;
    height: 33px;
    width: 150px;
    letter-spacing: 0.47px;
    background: #8ec5fc;
    background: -webkit-repeating-radial-gradient(
        circle farthest-corner at top center,
        #8ec5fc 0%,
        #e0c3fc 100%
    );
    background: -moz-repeating-radial-gradient(
        circle farthest-corner at top center,
        #8ec5fc 0%,
        #e0c3fc 100%
    );
    background: repeating-radial-gradient(
        circle farthest-corner at top center,
        #8ec5fc 0%,
        #e0c3fc 100%
    );
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 1px 1px 20px #4747475e;
}

.navigationBarButton {
    position: absolute;
    width: 110px;
    height: 40px;
    top: calc((100% - 40px) / 2);
    right: 20px;
    color: rgb(186, 196, 252);
    border: solid 3px transparent;
    background-image: repeating-radial-gradient(
            circle farthest-corner at top center,
            #8ec5fc 0%,
            #e0c3fc 100%
        ),
        linear-gradient(101deg, #8ec5fc, #e0c3fc);
    background-origin: border-box;
    background-clip: content-box, border-box;
    border-radius: 20px;
    font-family: "Helvetica";
    font-size: 14px;
    letter-spacing: 0.47px;
    font-weight: bold;
    box-shadow: 2px 1000px 1px #fff inset;
    transition: all 0.5s ease;
}

.navigationBarButton:hover {
    box-shadow: 2px 1000px 1px #fff inset, 3px 3px 10px #3d3d3d65;
}
